export default {

	data() {
		return {
			battery: 50,
			deviceId: '',
			mainUUID: '',
			writeUUID: '',
			notifyUUID: '',
			bluetoothConnected: false,
			wifiSSID: '',
			wifiPWD: '',
			defaultIndex: 1,
			workTypes: [{
					value: 1,
					name: '粉丝计数器'
				}, {
					value: 2,
					name: '天气预报'
				},

			],
			workType: 1
		}
	},
	onLoad() {
		//加载时，获取手机已经连接的wifi
		var that = this

		wx.startWifi({
			success(res) {
				console.info('开启wifi模块成功')
				wx.getConnectedWifi({
					complete(res) {
						console.log(res)
						//如果wifi连接上
						if (res.errCode == 0) {
							that.wifiSSID = res.wifi.SSID
						}
					}
				})
			}
		})




	},
	onShow() {
		//显示加载中
		// uni.showLoading({
		// 	title: '连接中',
		// 	mask: true
		// })
		//每次页面显示时连接蓝牙
		//this.connectBluetooth()

	},
	onHide() {
		//每次页面隐藏时关闭蓝牙
		//this.closeBluetooth(this.deviceId)
	},
	methods: {
		connectBle() {
			var that = this
			setTimeout(function() {
				that.btconnected = true
				uni.hideLoading()
			}, 4000)
		},
		/**
		 * 关闭蓝牙连接
		 * @param {Object} deviceId 设备id
		 */
		closeBluetooth(deviceId) {
			uni.closeBLEConnection({
				deviceId,
				success(res) {
					console.info('页面隐藏，断开蓝牙成功')
					uni.closeBluetoothAdapter({
						success(res) {
							console.info('页面隐藏关闭蓝牙适配器成功')
						}
					})
				}
			})

		},
		/**
		 * 连接蓝牙
		 */
		connectBluetooth() {
			var that = this
			try {
				const platform = uni.getStorageSync('platform');
				const version = uni.getStorageSync('version');
				if (platform && version) {
					uni.openBluetoothAdapter({
						success(res) {
							//蓝牙开关打开，直接连接蓝牙
							that.doConnect(platform, version)
						},
						fail(res) {
							uni.hideLoading()
							//蓝牙未打开，提示用户开启手机蓝牙
							uni.showModal({
								title: '提示',
								content: '使用小程序请开启手机蓝牙',
								showCancel: false,
								mask: true,
								success: function(res) {
									if (res.confirm) {
										console.log('用户点击确定');
									}
								}
							})

						}
					})
				}
			} catch (e) {
				console.error('连接蓝牙时获取系统信息失败')
				uni.showModal({
					title: '错误',
					content: '获取小程序系统信息失败，请重试',
					showCancel: false,
					success: function(res) {
						if (res.confirm) {
							uni.reLaunch({
								url: '../index/index'
							})
						}
					}
				})

			}
		},
		/**
		 * 真实连接蓝牙方法
		 */
		doConnect(platform, version) {
			var that = this
			//1. 开启搜索服务
			var searchTimeoutId = setTimeout(function() {
				uni.hideLoading()
				uni.stopBluetoothDevicesDiscovery({
					success(res) {
						console.info('蓝牙搜索超时，关闭搜索服务成功')
					}
				})
				if (that.deviceId == '') {
					uni.showModal({
						title: '提示',
						content: '未搜索到设备请靠近设备后重试',
						showCancel: false,
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '../index/index'
								})
							}
						}
					})
				}
			}, 5000)

			uni.startBluetoothDevicesDiscovery({
				services: [that.mainUUID],
				success(res) {
					var deviceId = res.deviceId
					//搜索到设备,关闭搜索服务
					uni.stopBluetoothDevicesDiscovery({
						success(res) {
							console.info('搜索到设备，关闭搜索服务成功')
						}
					})
					if ('android' == platform) {
						//安卓设备直接连接
						uni.createBLEConnection({
							deviceId,
							success(res) {
								console.info('创建蓝牙连接成功')
								that.bluetoothConnected = true
							}
						})

					} else {
						//ios设备要搜索一下特征才能使用特征
						uni.createBLEConnection({
							deviceId,
							success(res) {
								that.bluetoothConnected = true
								console.info('创建蓝牙连接成功')
								uni.getBLEDeviceServices({
									deviceId,
									success(res) {
										console.info('ios设备搜索蓝牙服务成功', res.services)
										//搜索特征
										uni.getBLEDeviceCharacteristics({
											deviceId,
											serviceId: that.mainUUID,
											success(res) {
												console.info('ios设备搜索特征成功', res)
											}
										})
									}
								})
							}
						})
					}

				}
			})


		},



	},
	computed: {
		btnmessage() {
			return this.btconnected ? '蓝牙连接中' : '保存'
		}
	}
}
